import React, { Component } from "react";
import { Container } from "semantic-ui-react";
import Menus from "./Menus";
import Footer from "./Footer";
import Navbar from "../components/Navbar";
import Gif from "../components/Gif";

export default class App extends Component {
  render() {
    const sections = [
      {
        id: "o-que",
        header: "O que?",
        content: (
          <p>
           Problemas na graduao  algo que  comum a todos ns, mas como bons CIners a gente odeia
           ficar s reclamando sem colocar a mo na massa e resolver os problemas que nos afligem.
           Sim, as vezes nos sentimos de mos atadas e sem saber o que fazer. Em sua primeira edio
           o HACKAPET traz o tema Educao focando em encontrar solues para ns mesmos, alunos do CIn.
          </p>
        )
      },
      {
        id: "para-quem",
        header: "Para quem?",
        content: (
          <p>
            O Hackathon busca integrar todos os alunos do CIn porm de formas diferentes. Os alunos do 1 ao 5 perodo (2016.1 at 2018.1)
            dos trs cursos podem participar como competidores e qualquer aluno pode participar como tutor
            ministrando workshops de tecnologias que sero usadas durante o evento.
          </p>
        )
      },
      {
        id: "como",
        header: "Como?",
        content: (
          <p>
            A ideia antes de tudo  trocar experincias e sobretudo aprender. Para isso, todo o processo ser acompanhado por
            uma equipe usando a metodologia Problem Based Learning - PBL. Metodologia usada pelo PET-Informtica em suas 
            atividades que usa soluo de problemas para gerar aprendizagem.
          </p>
        )
      },
      {
        id: "onde",
        header: "Onde?",
        content: (
          <p>
           Centro de Informtica - UFPE
          </p>
        )
      },
      {
        id: "quando",
        header: "Quando?",
        content: (
          <p>
           O HACKAPET vai acontecer nos dias 9 e 10 de agosto de 2018, totalizando 36 horas de imerso. 
          </p>
        )
      },
      {
        id: "mas-e",
        header: "Mas e...?",
        content: (
          <p>
             <p className="question">Mas eu no sei nada, como vou participar de um hackathon? <br/></p>
            Nosso intuito  exatamente esse! Aprender coisas novas colocando a mo na massa e resolvendo problemas. Alm disso,
            durante o evento sero oferecidos vrios workshops de variadas reas que voc pode aplicar no seu projeto e
            ir aprendendo junto com seu time. 

            <br/><br/>
            <p className="question">Como fica alimentao?<br/></p>
            Ns vamos oferecer alguns snacks durante o evento, mas as refeies so por sua conta. 

            <br/><br/>

            <p className="question">Preciso levar computador?<br/></p>
            Os computadores do CIn estaro disponveis para ns, mas fique  vontade para trazer seu beb. Os grads sero divulgados em breve.

            <br/><br/>

            <p className="question">No sou aluno do CIn, posso participar?<br/></p> 
            O evento  exclusivo para alunos dos cursos de Cincia da Computao, Engenharia da Computao e Sistemas da Informo
            do Centro de Informtica.

            <br/><br/>

            <p className="question">Quanto que vai custar?<br/></p> 
            As inscries sero abertas em breve e sero no valor de 30 reais. O evento  
            100% sem fins lucrativos e toda a renda obtida ser convertida para o prprio evento.

            <br/><br/>

            <p className="question">Preciso chegar com a equipe formada?<br/></p> 
            No. Quando levantarmos os problemas e quais as solues que devem ser desenvolvidas, as equipes podem ser formadas.
            Mas se voc j vier com seu time pronto, tambm no tem problema. 

            <br/><br/>

           
          </p>
        )
      },
      {
        id: "inscricoes",
        header: "Inscries",
        content: (
          <p>
            Inscries abertas!!
            <br/><br/>
            <a href="https://goo.gl/forms/EvdeUAlWhaDseMR13" target="_blank" rel="noopener noreferrer">Inscries para participantes</a>
            <br/>
            <a href="https://goo.gl/forms/BYhPSvYzhOrw0i843" target="_blank" rel="noopener noreferrer">Inscries para tutores</a>
          </p>          
        )
      }
    ];

    return (
      <div>
        <div className="index-page">
          <Gif />
          <div className="index-navbar">
            <Container text>
              <Navbar />
            </Container>
          </div>
          <Container text>
            <Menus sections={sections} />
          </Container>
        </div>
        <Footer />
      </div>
    );
  }
}



